$( document ).ready(function() {
    $('.nav__toggle').click(function() {
      $('.nav__content').slideToggle();
    })
    $('.toggle__item a').click(function(e) {
        e.preventDefault();
        var $this = $(this);
        $this.next('.nav__sub_top').slideToggle(350);
        $this.next('.nav__sub_top').toggleClass('show');
        $this.children('.icon').toggleClass('icon_active');

        $this.parent().children('.nav__sub_left').toggleClass('show');
        $this.parent().children('.nav__sub_left').slideToggle(350);
        $this.next('.icon').toggleClass('icon_active');
    })
});